.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "OpenSans";
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-weight: 600;
  src: url("../fonts/OpenSans-SemiBold.ttf") format("truetype");
  font-display: swap;
}
.page-body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0 auto;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 400;
  background-color: #f3f7fa;
  color: #4a4a4a;
}

.page-header__wrapper {
  width: 960px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
}

.page-header__logo {
  display: block;
  margin: 0 auto;
  width: 60px;
  height: 60px;
}

.page-main {
  flex: 1;
  width: 100%;
  display: flex;
}

.page-main__wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 754px;
  padding-left: 103px;
  padding-right: 103px;
}

.products {
  width: 502px;
}

.products__button-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  background-color: #ffffff;
  border: 1px solid #dfe5ec;
  border-radius: 5px;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  text-transform: uppercase;
  list-style: none;
}

.products__button-item {
  width: 166px;
  border-right: 1px solid #dfe5ec;
  text-align: center;
}
.products__button-item:last-child {
  border: none;
  border-top-right-radius: 10px;
}

.products__label {
  display: block;
  padding: 15px;
  cursor: pointer;
}

.products__input:checked + .products__label {
  background-color: #2196f3;
  color: #ffffff;
}

.products__show-ticket {
  margin: 0;
  padding: 15px 20px;
  width: 100%;
  background-color: #2196f3;
  color: #ffffff;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.products__show-ticket:hover {
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);
}
.products__show-ticket:active {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.filter {
  width: 232px;
  margin-right: 20px;
}

.form {
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.form__search {
  margin: 0;
  padding: 0;
  border: none;
}

.form__legend {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  text-transform: uppercase;
}

.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__item {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
}
.form__item:last-child {
  margin-bottom: 0;
}

.form__label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}

.form__input + .form__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: -1px;
  margin: auto;
  width: 18px;
  height: 18px;
  border: 1px solid #9abbce;
  border-radius: 2px;
}

.form__input:checked + .form__label::before {
  border: 1px solid #2196f3;
  background-image: url("../image/icon-filter/shape.svg");
  background-size: 12px 8px;
  background-position: center;
  background-repeat: no-repeat;
}

.form__input:hover + .form__label::before {
  border: 1px solid #2196f3;
}

.information-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 20px;
}

.information-list__item {
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.information-list__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.information-list__price {
  width: 160px;
  margin: 0;
  padding: 0;
  color: #2196f3;
  font-weight: 600;
  font-size: 24px;
  line-height: 24px;
}

.direction {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}
.direction:last-child {
  margin-bottom: 0;
}

.direction__wrapper {
  width: 141px;
}

.direction__title {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  color: #a0b0b9;
  text-transform: uppercase;
}

.direction__subtitle {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
}